* {
  box-sizing: border-box;
}

body {
  background: #9e9e9e63;
  font-family: 'Assistant', sans-serif !important;
}

.App {
  text-align: right;
}

.languageType {
  text-align: center;
  margin: 2px 1%;
}

.languageType a {
  text-decoration: none;
  color: black;
}

.languageType a:hover {
  text-decoration: none;
  color: white;
  background-color: rgb(32, 32, 32);
}

.bg-dark {
  direction: rtl;
  background-color: #7f5ea3 !important;
}

a {
  color: white;
  text-decoration: none;
  align-self: center;
  margin-left: 10px;
  margin-right: 10px;
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

a:hover {
  color: white;
  text-decoration: underline;
}

@media (min-width: 992px) {
  .navbar-expand-lg {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-flow: row nowrap;
    flex-flow: row nowrap;
    -webkit-box-pack: start;
    -ms-flex-pack: start;
    justify-content: flex-start;
    background-color: #7f5ea3 !important;
  }
}

.imageMain {
  margin-top: 1%;
  width: 100%;
  /* object-fit: cover; */
  height: 32vh;
  -o-object-fit: contain;
  object-fit: cover;
  box-shadow: 10px 5px 7px #d6c4a16e;
}


.homeContant {
  /* position: absolute; */
  width: 100%;
  height: 50vh;
  /* background-color: aqua; */
}

.partOne {
  /* position: relative; */
  float: left;
  width: 17%;
  height: 68vh;
  /* background-color: blue; */
  padding: 1%;
  margin-top: 2%;
  margin-left: 7%;
  border: 3px solid black;
}

.partTwo {
  /* position: relative; */
  margin-top: 3%;
  margin-right: 7%;
  float: right;
  width: 60%;
  height: 50vh;
  /* background-color: orange; */
  direction: rtl;
  text-align: right;
}


.mengPic {
  width: 80%;
  height: 20vh;
  object-fit: cover;
}

