input {
  margin-left: 2%;
}

.navbar-dark .navbar-brand {
  color: #fff;
  margin-right: 6%;
  font-weight: bold;
}

@media (min-width: 992px) {
  .navbar-expand-lg .navbar-collapse {
    display: -webkit-box !important;
    display: -ms-flexbox !important;
    display: flex !important;
    -ms-flex-preferred-size: auto;
    flex-basis: auto;
    margin-left: 6%;
  }
}

.contactUs {
  background: #7f5ea3bf;
  padding: 2% 1%;
  color: white;
}


.carousel {
  width: 49%;
  height: 50%;
  left:33%;
}

